  }
  .communction-container .content-column-communction .inner-content-communction .conent-col1 h1 {
margin-left: 30%;
padding-top: 25px;
  }
  .communction-container .content-column-communction .inner-content-communction .conent-col1 .button{
    margin-left:30%
  }
}


