.talent-container {
  display: flex;
  background: #b0d6ce;
}
.talent-img {
  width: 40%;
  background-image: url("https://client.andela.com/assets/global/img_pattern_login.png");
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 625px;
}
.talent-img img {
  vertical-align: middle;
  width: 100%;
  max-height: unset !important;
  margin-left: auto;
}
.talent-nvbar {
  width: 100%;
  background-color: rgba(245, 245, 245);
}
.talent-form {
  width: 100%;
  background-color: rgba(245, 245, 245);
}
.talent-nvbar {
  padding-top: 1rem !important;
  padding-bottom: 1rem !important;
  background-color: #fff;
  width: 100%;
  justify-content: space-between;
  display: flex;
  padding-left: 8.75rem;
  padding-right: 8.75rem;
}
.talent-nvbar img {
  width: 106px;
  height: 32px;
}
.talent-nvbar .talent-btn {
  background-color: transparent;
  color: #132128 !important;
  border: 1px solid #132128;
  box-shadow: inset 0 0px 0px 0.5px #132128 !important;
  min-width: 8.5rem;
  height: 3rem;
  justify-content: center;
  align-items: center;
  transition: background-color 0.2s ease-in, border-color 0.2s ease-in-out,
    color 0.2s ease-in-out;
  border-radius: 50rem;
  padding: 0 1rem;
  font-size: 1rem;
  text-decoration: none;
  vertical-align: middle;

  font-weight: 600 !important;
  line-height: 1.5;
  display: inline-flex !important;
}
.talent-content {
  width: 100%;
  max-width: 960px;
  padding-bottom: 88px;
}
.talent-content form {
  padding-bottom: 1.5rem;
  width: 100%;
  /* display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; */
  width: 680px;
  margin-left: 15%;
}
.talent-content form h1 {
  text-align: left;
  font-size: 1.5rem;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
}
.talent-content .conent {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.talent-content .conent h3 {
  font-size: 1.25rem;
  text-align: left;
  margin-bottom: 0;
}
.talent-content .conent .checkbox-talent {
  gap: 0.5rem;
  display: flex;
  align-items: center;
}
.talent-content .conent .checkbox-talent input {
  -webkit-appearance: none;
  border: 1px solid #132128;
  cursor: pointer;
  min-width: 1rem;
  min-height: 1rem;
  border-radius: 0.25em;
}
.talent-content .conent .checkbox-talent label {
  cursor: pointer;
  font-size: 0.875rem;
  color: rgb(19, 33, 40);
}
.talent-content .talent-choices {
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * 1rem);
  margin-right: calc(-0.5 * 0.5rem);
  margin-left: calc(-0.5 * 0.5rem);
}
.talent-content .talent-choices .talent-choices-1 {
  max-width: 100%;
  padding-right: calc(0.5rem * 0.5);
  padding-left: calc(0.5rem * 0.5);
  margin-top: 1rem;
  /* width:100% */
}
.talent-content .talent-choices .talent-choices-1 label {
  gap: 20px;
  border: 1px solid #b8bcbf;
  border-radius: 2rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  background-color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: #424d53;
}
.talent-content .talent-choices .talent-choices-1 label input[type="radio"] {
  width: 24px;
  height: 24px;
  border: 1px solid #132128;
  border-radius: 50%;
  vertical-align: top;
  background-color: #ffffff;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.talent-content
  .talent-choices
  .talent-choices-1
  label
  input[type="radio"]::before {
  content: "";
  display: block;
  width: 60%;
  height: 60%;
  border-radius: 50%;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.talent-content .talent-choices .talent-choices-1 .last-label {
  gap: 0.5rem;
  border: 1px solid #b8bcbf;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  background-color: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.talent-content .talent-choices .talent-choices-1 .talent-icon {
  color: #b8bcbf;
}
.talent-content
  .talent-choices
  .talent-choices-1
  label
  input[type="radio"]:checked::before {
  box-shadow: 0 0 5px #198038;
  color: #198038;
}
button {
  background-color: #56c870;
  color: #132128;
  border: unset;
  min-width: 8.5rem;
  height: 3rem;
  justify-content: center;
  align-items: center;
  transition: background-color 0.2s ease-in, border-color 0.2s ease-in-out,
    color 0.2s ease-in-out;
  border-radius: 50rem;
  display: inline-flex;
  margin-top: 40px;

  font-weight: 600;
}
button:hover {
  border-color: 1px solid #132128;
  background-color: #61d67d;
}
.handel-error-talent {
  color: #da1e28;
  margin-left: 1%;
  text-align: left;
  font-size: 12px;
}
.talent-content h1 {
  color: #132128;
}
@media only screen and (max-width: 600px) {
  .talent-container {
    display: flex;
    flex-direction: column;
  }
  .talent-content {
    margin-left: -6%;
  }
  .talent-nvbar {
    width: 100%;
    position: absolute;
    top: 0;
  }
  .talent-img {
    height: 400px;
    /* width: 100%; */
  }
  .talent-img img {
    vertical-align: left;
    width: 60%;
    max-height: unset !important;
    margin-left: auto;

    float: left;
  }
  .talent-nvbar img {
    width: 106px;
    height: 32px;
    margin-left: -50%;
  }
  .talent-nvbar .talent-btn {
    margin-left: 85%;
  }
  .talent-choices {
    display: flex;
    flex-direction: column;
  }
  .talent-choices-1 {
    width: 430px;
  }
  .conent {
    display: flex;
    flex-direction: column;
  }
}

@media only screen and (min-width: 600px) and (max-width: 992px) {
  .conent {
    display: flex;
    flex-direction: column;
  }
  .talent-container {
    display: flex;
    flex-direction: column;
  }
  .talent-content {
    margin-left: -6%;
  }
  .talent-nvbar {
    width: 100%;
    position: absolute;
    top: 0;
  }
  .talent-img {
    height: 400px;
    width: 100%;
  }
  .talent-img img {
    vertical-align: left;
    width: 60%;
    max-height: unset !important;
    margin-left: auto;

    float: left;
  }
  .talent-nvbar img {
    width: 106px;
    height: 32px;
    margin-left: -5%;
  }
  .talent-nvbar .talent-btn {
    margin-left: 70%;
  }
  .talent-choices {
    display: flex;
    flex-direction: column;
  }
  .talent-choices-1 {
    width: 430px;
  }
}

@media only screen and (min-width: 768px) {
  .talent-nvbar img {
    width: 106px;
    height: 32px;
    margin-left: -5%;
  }
  .talent-nvbar .talent-btn {
    margin-left: 80%;
  }
}

@media only screen and (min-width: 1200px) {
  .conent {
    display: flex;
    flex-direction: row;
  }
}
